import React, { useState, useEffect, useCallback } from 'react'

import styled from 'styled-components'

import { useSwipeable } from 'react-swipeable'

import { ArrowForwardIos, PlayCircleOutline } from '@mui/icons-material';

const CardOutline = styled.div`

    border: 3px solid #1C6EA4;
    border-radius: 10px;
    width: 70%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;

`

const FrontSide = styled.div`

    display: ${props => props.front === 'show' ? 'flex' : 'none'};
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

    width: 90%;
    height: 80%;

    text-align: center;

`

const Word = styled.p`

    font-size: 2rem;
    font-weight: 700;
    overflow-wrap: break-word;

`

const PartOfSpeech = styled.p`

    font-size: 1.5rem;
    font-weight: 500;
    font-style: italic;

`

const BackSide = styled.div`

    display: ${props => props.back === 'show' ? 'flex' : 'none'};
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

    width: 90%;
    height: 80%;

    text-align: center;

    overflow: auto;

`

const Meaning = styled.p`

    font-size: 1.3rem;
    font-weight: 600;

`

const Example = styled.p`

    font-size: 1rem;
    font-weight: 500;
    font-style: italic;

`

const Pronounce = styled.audio`

    display: none;
    opacity: 1;
    width: 200px;

`

const FlashCard = ({ words }) => {

    const wordsLength = words.length

    const randomWordIndex = Math.floor(Math.random()*(wordsLength+1))

    const [front, setFront] = useState('show')

    const [back, setBack] = useState('hide')
    
    const [wordIndex, setWordIndex] = useState(randomWordIndex)

    const Flip = useCallback(() => {

        front === 'show' ? setFront('hide') : setFront('show')

        back === 'show' ? setBack('hide') : setBack('show')

    }, [back, front])

    const Next = useCallback(() => {

        const nextRandomWordIndex = Math.floor(Math.random()*(wordsLength+1))
        setWordIndex(nextRandomWordIndex)
        setFront('show')
        setBack('hide')

    }, [wordsLength])

    const handlers = useSwipeable({
        onSwipedLeft: () => Next(),
        swipeDuration: 300,
        preventScrollOnSwipe: true
    })

    const PlayPronounce = useCallback((event) => {
        event.stopPropagation()
        const audio = document.querySelector('audio')
        audio.load()
        audio.play()
        
    }, [])

    const KeyPlayPronounce = useCallback((event) => {
        const audio = document.querySelector('audio')
        audio.load()
        audio.play()
        
    }, [])


    useEffect(() => {
        const handler = (e) => {
            if (e.key === "ArrowRight") {
                Next()
            }

            if (e.key === " ") {
                Flip()
            }

            if (e.key === "s" && back === "show") {
                KeyPlayPronounce()
            }
        }

        window.addEventListener("keydown", handler, false)
        return () => window.removeEventListener("keydown", handler, false)
    }, [Next, Flip, KeyPlayPronounce, back])


    return (

        <>
            <CardOutline 
                onClick = {Flip}
                {...handlers}
            >
         
                <FrontSide front = {front}>
                    
                    <Word>
                        {words[wordIndex].Vocabulary}
                    </Word>
                    
                    <PartOfSpeech>
                        {words[wordIndex].PartOfSpeech}
                    </PartOfSpeech>
                
                </FrontSide>

                <BackSide back = {back}>

                    <Meaning>
                        {words[wordIndex].Meaning}
                    </Meaning>

                    <Example>
                        Eg: {words[wordIndex].Example}
                    </Example>
                    <PlayCircleOutline 
                        onClick = {PlayPronounce}
                        sx = {{
                            fontSize: 40
                        }}
                        style = {{
                            zIndex: 99
                            }}
                        
                    />
                    <Pronounce controls>
                        <source 
                            src = {words[wordIndex].Pronounce}
                            type = "audio/mpeg" />
                    </Pronounce>

                </BackSide>

            </CardOutline>
            
            <ArrowForwardIos 

                sx = {{fontSize: 40, position: 'absolute', right: 10}}
                onClick = {Next}

            />

        </>

    )

}

export default FlashCard