import React from 'react'

import { BrowserRouter, Routes, Route } from 'react-router-dom'

import HomePage from './component/HomePage';
import FlashCardData from './component/FlashCardData';
import Dict from './component/Dict'

function App() {


  return (
    <BrowserRouter basename='/ieltsquest'>

        <Routes>

          <Route

              path = '/'
              exact
              element = {<HomePage />}

          ></Route>

          <Route

              path = '/flashcard'
              element = {<FlashCardData />}

          />

          <Route

              path = '/dict'
              element = {<Dict />}

          />

        </Routes>
     

    </BrowserRouter>
  );
}

export default App;
