import React, { useState } from 'react'

import axios from 'axios';

import styled from 'styled-components';

import bgPic from './c12f31ab8121d736f4cac5c66c36f5b4.jpeg'

import { useForm } from 'react-hook-form'

import { FormInput } from './InputForm';

import { Stack } from '@mui/material'

import { Search, PlayCircleOutline, Home } from '@mui/icons-material';

import LoadingPic from './Bean-Eater-1s-200px.gif'
import { useNavigate } from 'react-router-dom';

const BackGround = styled.div`

    background-image: url(${bgPic});
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

`

const DataWrapper = styled.div`

    width: 90%;
    height: 60vh;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    margin-bottom: 30px;

`

const WordWrapper = styled.div`

    border: 2px solid red;
    border-radius: 10px;
    width: 100%;
    height: 45vh;
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;

`

const ContentWrapper = styled.div`

    width: 95%;
    height: 43vh;
    overflow: auto;

`

const ShowWord = styled.p`

    font-size: 2rem;
    font-weight: 800;

`

const WordType = styled.p`

    font-size: 1rem;
    font-style: italic;
    font-weight: 500;
    text-transform: lowercase;

`

const WordInfoWrapper = styled.div`

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

`

const Dict = () => {

    const navigate = useNavigate()

    const defaultValues = {
        word: ""
    }

    const [ wordSearch, setWordSearch ] = useState('')
    const [ wordMeaning, setWordMeaning ] = useState('')
    const [ wordType, setWordType ] = useState('')
    const [ wordAudio, setWordAudio ] = useState('')
    const [ wordExamples, setWordExamples ] = useState([])
    const [loading, setLoading] = useState(false)

    const methods = useForm({defaultValues: defaultValues})

    const {control, handleSubmit} = methods

    const onSubmit = (data) => {
        setLoading(true)

        axios.post('https://rose-careful-bull.cyclic.app/dict', {
            word: data.word.toLowerCase()
        })
            .then(results => {
                setWordSearch(results.data.word)
                setWordType(results.data.type)
                setWordMeaning(results.data.meaning)
                setWordExamples(results.data.examples)
                setWordAudio(results.data.audio.filter(item => item.type === "audio/mpeg" && item.url.includes("us_pron"))[0].url)
                setLoading(false)
            })
            .catch(console.error)

    }

    const PlayPronounce = (event) => {
        event.stopPropagation()
        const audio = document.querySelector('audio')
        audio.load()
        audio.play()
        
    }

    return (

        <>

            <BackGround>
                <Home 

                    onClick = {() => navigate('/')}
                    style = {{
                        fontSize: '2rem',
                        position: 'absolute',
                        left: 20,
                        top: 20
                    }}

                />
                <DataWrapper>
                <Stack
                    style = {{
                        marginTop: 20,
                        width: '100%'
                    }}
                >
                    <form onSubmit={handleSubmit(onSubmit)}>
                    <FormInput 
                        
                        name = {"word"}
                        control = {control}
                        label = {"Search here"}
                        id = {"outlined-basic-word"}
                        variant = {"outlined"}
                        type = {"text"}
                        required = {true}
                        autoComplete = {"current-word"}
                        
                        
                    />

                    <Search 
                        onClick = {handleSubmit(onSubmit)}
                        id = "search-btn"
                        style = {{
                            position: 'absolute',
                            right: 30,
                            top: 150,
                            fontSize: '2rem'
                        }}
                    />

                    <button type = "submit" style = {{display: 'none'}}></button>
                    
                    </form>
                    

                </Stack>
                
                        <WordWrapper>
                        {loading && <img src = {LoadingPic} alt = "" style = {{alignSelf: 'center'}}/>}
                            <ContentWrapper>
                                <WordInfoWrapper>
                                    <Stack>
                                        <ShowWord>{wordSearch}</ShowWord>
                                         <WordType>{wordType}</WordType>
                                     </Stack>
                                    {wordSearch && <PlayCircleOutline 
                                        onClick = {PlayPronounce}
                                        style = {{
                                            fontSize: '50'
                                        }}
                                    />}
                                        <audio 
                                            controls
                                            style = {{
                                                display: 'none'
                                            }}    
                                        >
                                            <source src = {wordAudio} type = 'audio/mpeg' />
                                        </audio>
                                </WordInfoWrapper>

                            <p
                                style = {{
                                    fontSize: '1.5rem',
                                    fontWeight: 600,
                                    marginTop: '10px'
                                }}
                            >
                                {wordMeaning}
                            </p>
                                
                            <ol
                                style = {{
                                    marginTop: '10px',
                                    fontWeight: 400,
                                    fontStyle: 'italic',
                                    overflow: 'auto',
                                    height: '20vh',
                                    listStyleType: 'decimal',
                                    lineHeight: '1.5'
                                }}
                            >
                               {wordExamples.map((item, index) => (
                                    <li key = {index} style = {{
                                        marginTop: '3px'
                                    }}>
                                        {item}
                                    </li>
                               ))} 
                            </ol>

                            

                            </ContentWrapper>
                        </WordWrapper>
                        
                   
                    </DataWrapper>
            </BackGround>

        </>

    )

}

export default Dict;