import React from 'react'

import TextField from '@mui/material/TextField'
import { Controller } from 'react-hook-form'

export const FormInput = ({ name, control, label, id, variant, type, required, autoComplete, disabled }) => {

    return (

        <Controller

            name = {name}
            control = {control}
           
            render = {({ field: {onChange, value}}) => (
                <TextField 
                    onChange = {onChange}
                    value = {value}
                    label = {label}
                    id = {id}
                    variant = {variant}
                    type = {type}
                    required = {required}
                    autoComplete = {autoComplete}
                    disabled = {disabled}
                    fullWidth
                />
            )}
        />

    )

}